/* слайдер */

.slider {
    background-color: #10C9C3;
}

/* временном */


.slider__wrapper {

    padding-left: 260px;
    padding-right: 260px;
    padding-top: 174px;
}

.slider__item--text {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    padding-bottom: 29px;
    color: #FFFFFF;
    letter-spacing: 0.3px;
}
.slider__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.slider__item--autor {
    text-align: center;
    padding-bottom: 107px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 36px;
    color: #FFFFFF;
    letter-spacing: 1.2px;
    text-transform: uppercase;
}
.slider__bar {
    padding-bottom: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.slider__bar--container {
    width: 43px;
    height: 13px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    align-self: auto;
}
.slider__btn {
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #FFFFFF;
    align-content: center;
    opacity: 0.6;
}
.slider__btn:hover {
    width:13px;
    height: 13px;
    border-radius: 50%;
    opacity: 1;
    transition: .5;
    cursor: pointer;
}
